// MEDIA CAROUSEL
// ------------------------------------------------------------
if ($('.media-carousel').length) {
    var mediaCarousel = new Swiper(".media-carousel", {
        slidesPerView: 2.5,
        speed: 1000,
        spaceBetween: 30,
        loop: true,
        modules: [Carousel.Navigation, Carousel.Pagination, Carousel.Autoplay],
        autoplay: {
            delay: 5000,
            disableOnInteraction: false,
        },
        navigation: {
            nextEl: ".similar-next",
            prevEl: ".similar-prev"
        },
        pagination: {
            el: ".media-pagination",
            clickable: true,
        },
        breakpoints: {
            0: {
                slidesPerView: 1,
            },
            600: {
                slidesPerView: 1,
            },
            768: {
                slidesPerView: 2,
            },
            769: {
                slidesPerView: 2,
            },
            992: {
                slidesPerView: 2,
            },
            1200: {
                slidesPerView: 2,
            },
            1201: {
                slidesPerView: 2.5,
            },
        },
    })
    let totalCountField = $('.media-carousel .total-count-field')
    let total = $('.media-carousel .swiper-slide:not(.swiper-slide-duplicate)').length;
    totalCountField.find('.current').text(mediaCarousel.realIndex + 1)
    totalCountField.find('.total').text(total)
    mediaCarousel.on('slideChange', function () {
        totalCountField.find('.current').text(mediaCarousel.realIndex + 1)
    })
}

