// HOMEPAGE LATEST CAROUSEL
// ------------------------------------------------------------
if ($('.homepage-latest-carousel').length) {
    var homepageLatestCarousel = new Swiper(".homepage-latest-carousel", {
        slidesPerView: 4,
        speed: 1000,
        spaceBetween: 0,
        loop: true,
        modules: [Carousel.Navigation, Carousel.Pagination, Carousel.Autoplay],
        autoplay: {
            delay: 5000,
            disableOnInteraction: false,
        },
        navigation: {
            nextEl: ".homepage-latest-next",
            prevEl: ".homepage-latest-prev"
        },
        breakpoints: {
            0: {
                slidesPerView: 1,
            },
            481: {
                slidesPerView: 2,
            },
            1024: {
                slidesPerView: 2,
            },
            1200: {
                slidesPerView: 3,
            },
            1201: {
                slidesPerView: 4,
            },
        },
    })
    let latestSlide = $('.homepage-latest-field .container .wrapper .homepage-latest-carousel .swiper-wrapper .swiper-slide')
    let latestBGIMG = $('.homepage-latest-field .container .wrapper .homepage-latest-carousel .background-image')
    let hoverTimeOut
    latestSlide.hover(
        function () {
            let image = $(this).attr('data-image');

            latestBGIMG.find('.img').addClass('active');

            clearTimeout(hoverTimeOut);
            hoverTimeOut = setTimeout(() => {
                latestBGIMG.find('img').attr('src', image);

                let loadedImage = document.createElement('img');
                loadedImage.src = image

                loadedImage.onload = function () { latestBGIMG.find('.img').removeClass('active'); }
            }, 450);
        },
        function () { }
    )
}
// HOMEPAGE MAIN CAROUSEL
// ------------------------------------------------------------
if ($('.main-carousel').length) {
    var mainCarousel = new Swiper(".main-carousel", {
        slidesPerView: 1,
        speed: 1000,
        spaceBetween: 0,
        loop: true,
        modules: [Carousel.Navigation, Carousel.Pagination, Carousel.Autoplay, Carousel.EffectCreative],
        autoplay: {
            delay: 5000,
            disableOnInteraction: false,
        },
        effect: 'creative',
        creativeEffect: {
            prev: {
                scale: 1.5,
                opacity: 0,
                shadow: true,
            },
            next: {
                scale: 1.5,
                opacity: 0,
                shadow: true,
            },
        },
        navigation: {
            nextEl: ".main-next",
            prevEl: ".main-prev"
        },
        pagination: {
            el: ".main-pagination",
            clickable: true,
            renderBullet: function (index, className) {
                return '<div class="' + className + '">' + '<div class="content"><span class="value">' + (index + 1) + '</span><span class="icon"><svg version="1.1" id="Group_14" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="100px" y="100px"viewBox="0 0 37 37" style="enable-background:new 0 0 37 37;" xml:space="preserve"><g id="icon-circle"><circle class="circle-outer" cx="18.5" cy="18.5" r="18" fill="none" stroke="#ffffff50" /><circle class="circle-inner active" cx="18.5" cy="18.5" r="18" fill="none" stroke="#ffffff" /></g></svg></span></div></div>';
            },
        },
    })
    let sliderText = $(".homepage-slider-field .wrapper .main-carousel .swiper-wrapper .swiper-slide .image-field .text-container .text-wrapper .active-text")
    mainCarousel.on('slideChange', function () {
        sliderText.removeClass('active-text')
        setTimeout(() => {
            sliderText.addClass('active-text')
        }, 450);
        $('.circle-inner').removeClass('active');
        setTimeout(() => {
            $('.circle-inner').addClass('active');
        }, 100);
    })
}

