// PROJECT MEDIA CAROUSEL
// ------------------------------------------------------------
if ($('.project-media-carousel').length) {
    var projectMediaCarousel = new Swiper(".project-media-carousel", {
        slidesPerView: 1.8,
        speed: 1000,
        spaceBetween: 30,
        loop: true,
        modules: [Carousel.Navigation, Carousel.Pagination, Carousel.Autoplay],
        autoplay: {
            delay: 5000,
            disableOnInteraction: false,
        },
        navigation: {
            nextEl: ".project-media-next",
            prevEl: ".project-media-prev"
        },
        pagination: {
            el: ".project-media-pagination",
            clickable: true,
        },
        breakpoints: {
            0: {
                slidesPerView: 1,
            },
            600: {
                slidesPerView: 1,
            },
            768: {
                slidesPerView: 2,
            },
            769: {
                slidesPerView: 2,
            },
            992: {
                slidesPerView: 2,
            },
            1200: {
                slidesPerView: 2,
            },
            1201: {
                slidesPerView: 1.8,
            },
        },
        on: {
            slideChange: function () {
                var activeIndex = this.activeIndex;
                var realIndex = this.slides.eq(activeIndex).attr('data-swiper-slide-index');
                $('.swiper-slide').removeClass('swiper-slide-prev-prev swiper-slide-next-next');
                $('.swiper-slide[data-swiper-slide-index="' + realIndex + '"]').prev().prev().addClass('swiper-slide-prev-prev');
                $('.swiper-slide[data-swiper-slide-index="' + realIndex + '"]').next().next().addClass('swiper-slide-next-next');
            },
        }
    })
}

