// GSAP -------------------------------------------------------------
import gsap from 'gsap';
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import ScrollTrigger from 'gsap/ScrollTrigger'
import DrawSVGPlugin from 'gsap/DrawSVGPlugin'
gsap.registerPlugin(ScrollToPlugin, ScrollTrigger, DrawSVGPlugin);
window.ScrollTrigger = ScrollTrigger;
window.gsap = gsap;

if ($('.homepage-about-field').length) {
    let hafShape = $('.homepage-about-field .shape')
    if (windowWidth > 1440) {
        gsap.to(hafShape, {
            x: 100,
            scrollTrigger: {
                trigger: ".homepage-about-field",
                start: "center center",
                end: "bottom bottom",
                scrub: 5,
            }
        })
    }
}

if ($('.homepage-testimonial-field').length) {
    let htfIMGOne = $('.homepage-testimonial-field .container .wrapper .image-field .image--one')
    let htfIMGTwo = $('.homepage-testimonial-field .container .wrapper .image-field .image--two')
    if (windowWidth > 1200) {
        gsap.to(htfIMGTwo, {
            y: htfIMGOne.height() - (htfIMGTwo.height() / 2),
            scrollTrigger: {
                trigger: ".homepage-testimonial-field",
                start: "center center",
                end: "bottom bottom",
                scrub: 5,
            }
        })
    }
}

if ($('.about-company-field').length) {
    let acfIMGOne = $('.about-company-field .container .wrapper .image-field .image--one')
    let acfIMGTwo = $('.about-company-field .container .wrapper .image-field .image--two')
    if (windowWidth > 1200) {
        gsap.to(acfIMGTwo, {
            y: acfIMGOne.height() - (acfIMGTwo.height() - 80),
            scrollTrigger: {
                trigger: ".about-company-field",
                start: "center center",
                end: "bottom bottom",
                scrub: 5,
            }
        })
    }
}

if ($('.about-mission-field').length) {
    let amsLI = $('.about-mission-field .container .wrapper .text-field ul li')
    if (windowWidth > 1024) {
        $.each(amsLI, function (key, value) {
            gsap.to($(this), {
                scrollTrigger: {
                    trigger: $(this),
                    start: 'top center',
                    end: '100% center',
                    ease: 'power1.inOut',
                    scrub: true,
                    toggleActions: "restart pause reverse pause",
                    toggleClass: 'active'
                },
            })
        })
    } else {
        amsLI.addClass('active')
    }
    let amsRope = $('.about-mission-field .container .wrapper .image-field .image--rope')
    if (windowWidth > 1024) {
        gsap.to(amsRope, {
            x: -100,
            scrollTrigger: {
                trigger: ".about-mission-field",
                start: "center center",
                end: "bottom bottom",
                scrub: 5,
            }
        })
    }
}
if ($('.project-box').length) {
    let projectBox = $('.project-box')
    if (windowWidth > 1024) {
        $.each(projectBox, function (key, value) {
            gsap.to($(this).find('.image-field .img'), {
                x: -100,
                scale: 1.1,
                scrollTrigger: {
                    trigger: $(this),
                    start: "center center",
                    end: "bottom bottom",
                    scrub: 2,
                    toggleActions: "restart pause reverse pause",
                }
            })
        })
    }
}


if ($('.blog-box-field').length) {
    if (windowWidth > 1024) {
        let solutionsField = $('.blog-box-field');
        let solutionsOdd = $('.blog-box-field .blog-box:nth-child(odd)');
        let solutionsEven = $('.blog-box-field .blog-box:nth-child(even)');

        let solutionsTL = gsap.timeline({
            scrollTrigger: {
                trigger: solutionsField,
                start: 'top-=50% center',
                end: 'bottom+=50% center',
                scrub: 1.5,
            }
        });

        solutionsTL.to(solutionsOdd, { y: '-15%' }, 'same')
        solutionsTL.to(solutionsEven, { y: '15%' }, 'same')
    }
}