// MENU
// ------------------------------------------------------------
if ($('.menu-field').length) {
    let hasMenu = $('.menu-field .menu-ul .menu-li.has-menu')
    let megaMenuClose = $('.mega-menu .close')
    hasMenu.click(function () {
        $(this).addClass('active')
        megaMenuClose.closest('.mega-menu').addClass('active')
        $('.bg-overlay').addClass('active')
    })
    $('.bg-overlay').click(function () {
        $('.bg-overlay').removeClass('active')
        hasMenu.removeClass('active')
        megaMenuClose.closest('.mega-menu').removeClass('active')
    })
    megaMenuClose.click(function () {
        hasMenu.removeClass('active')
        $(this).closest('.mega-menu').removeClass('active')
        $('.bg-overlay').removeClass('active')
    })
    let megaMenuLI = $('.mega-menu .mega-menu-inner .mega-menu-field .mm-ul .mm-li')
    let megaMenuIMG = $('.mega-menu .mega-menu-inner .image-field')
    let megaMenuSRC, hoverTimeOut
    megaMenuLI.hover(
        function () {
            let image = $(this).attr('data-image');

            megaMenuIMG.find('.img').addClass('active');

            clearTimeout(hoverTimeOut);
            hoverTimeOut = setTimeout(() => {
                megaMenuIMG.find('img').attr('src', image);

                let loadedImage = document.createElement    ('img');
                loadedImage.src = image

                loadedImage.onload = function () { megaMenuIMG.find('.img').removeClass('active'); }
            }, 450);
        },
        function () { }
    )
    let language = $('.language')
    let megaMenu = $('.mega-menu .mega-menu-inner .mega-menu-field .mm-ul')
    let menuUL = $('.menu-field .menu-ul')
    if (windowWidth < 768) {
        megaMenu.append(language)
    } else {
        menuUL.first().append(language)
    }
    $(window).resize(function () {
        let windowWidthResize = window.innerWidth
        if (windowWidthResize < 768) {
            megaMenu.append(language)
        } else {
            menuUL.first().append(language)
        }
    })
    if (windowWidth < 900) {
        hasMenu.click(function () {
            $('body').addClass('overflow-hidden')
        })
        megaMenuClose.click(function () {
            $('body').removeClass('overflow-hidden')
        })
        $('.mega-menu .mega-menu-inner .mega-menu-field .mm-ul').addClass('scrollbar').css({
            'max-height': windowHeight - 100
        })
    }
}

// BLOG CONTROLLER
// ------------------------------------------------------------
if ($('.blog-controller').length) {
    // Blog Font Size Change
    var blog_size = 0;
    $('.blog-controller .size').click(function () {
        blog_size++;

        if (blog_size > 3) blog_size = 0;

        if (blog_size == 0) {
            $(this).closest('.page-content').find('.editor').find('p').css('font-size', '1.125rem')
        }
        if (blog_size == 0) {
            $(this).closest('.page-content').find('.editor').find('li').css('font-size', '1.125rem')
        }
        if (blog_size == 1) {
            $(this).closest('.page-content').find('.editor').find('p').css('font-size', '1.25rem')
        }
        if (blog_size == 1) {
            $(this).closest('.page-content').find('.editor').find('li').css('font-size', '1.25rem')
        }
        if (blog_size == 2) {
            $(this).closest('.page-content').find('.editor').find('p').css('font-size', '1.5rem')
        }
        if (blog_size == 2) {
            $(this).closest('.page-content').find('.editor').find('li').css('font-size', '1.5rem')
        }
        if (blog_size == 3) {
            $(this).closest('.page-content').find('.editor').find('p').css('font-size', '1.75rem')
        }
        if (blog_size == 3) {
            $(this).closest('.page-content').find('.editor').find('li').css('font-size', '1.75rem')
        }
    });
    // Blog Print
    $('.blog-controller .print').click(function () {
        $('.editor-field .editor').printThis()
    })
    // Blog URL Copy
    var copy_interval;
    var $url = $(location).attr('href');
    var $temp = $("<input>");
    $('.blog-controller .link').click(function () {
        $(this).css('pointer-events', 'hidden');
        $(this).addClass('copied')
        $("body").append($temp)
        $temp.val($url).select()
        document.execCommand("copy")
        $temp.remove()
        clearInterval(copy_interval)
        copy_interval = setInterval(() => {
            $(this).css('pointer-events', 'unset')
            $(this).removeClass('copied')
        }, 1000)
    })

    var copy_interval;
    var $url = $(location).attr('href');
    var $temp = $("<input>");
    $('.blog-controller .link').click(function () {
        $(this).css('pointer-events', 'hidden')
        $(this).addClass('copied')
        $("body").append($temp)
        $temp.val($url).select();
        document.execCommand("copy")
        $temp.remove();
        clearInterval(copy_interval);
        copy_interval = setInterval(() => {
            $(this).css('pointer-events', 'unset')
            $(this).removeClass('copied')
        }, 1000);
    })
}
