if ($('.project-filter-field').length) {
    var $isotope = $('.project-list-field .wrapper').isotope({
        // options
        itemSelector: '.project-box',
        layoutMode: 'fitRows'
    });
    var $box = $(".project-list-field .wrapper").isotope({
        itemSelector: ".project-box"
    });
    var isotopeToolbar = $('.filter-list')
    isotopeToolbar.on("click", "li", function () {
        var filterValue = $(this).attr("data-type");
        isotopeToolbar.find(".active").removeClass("active");
        $(this).addClass("active");
        if (filterValue !== "*") {
            filterValue = '[data-type="' + filterValue + '"]';
        }
        $box.isotope({
            filter: filterValue
        });
    });
}

