// PROJECT - 360 VIEW
// ------------------------------------------------------------
if ($('.threed-field')) {
    // 360 Field
    var threeDIMG = $('.threed-field .img')
    var objField = $('.threed-field .obj-field')
    threeDIMG.on('click', function () {
        $(this).closest('.placeholder-image').addClass('hidden')
        objField.addClass('active')
    })

    // Resize Model Height
    if ($(window).width() < 1367 && $(window).width() > 1200) {
        var modelImagesWidth = $('#model .model-images').outerWidth();
        modelImagesWidth = (modelImagesWidth * 9) / 20;
        /* $('#model .model-images').height(modelImagesWidth); */
    } else {
        var modelImagesWidth = $('#model .model-images').outerWidth();
        modelImagesWidth = (modelImagesWidth * 9) / 16;
        /* $('#model .model-images').height(modelImagesWidth); */
    }

    $(window).resize(function () {
        if ($(window).width() < 1367 && $(window).width() > 1200) {
            var modelImagesWidth = $('#model .model-images').outerWidth();
            modelImagesWidth = (modelImagesWidth * 9) / 20;
            /* $('#model .model-images').height(modelImagesWidth); */
        } else {
            var modelImagesWidth = $('#model .model-images').outerWidth();
            modelImagesWidth = (modelImagesWidth * 9) / 16;
            /* $('#model .model-images').height(modelImagesWidth); */
        }
    })

    // Image Change Function and Model Click Drag
    var numModel = $('#model .model-images .model-image').length;
    var modelGeneralWidth = $('.model-images').outerWidth();
    var modelClick = false;
    var clickedXaxis = '';
    var currentXaxis = '';
    var imageIDs = [];
    var i = 0;
    var currentIndex = 0;

    // ------- Image Change Function
    $('#model .model-images .model-image').each(function () {
        imageIDs[i] = $(this).attr('id');
        i++;
    });

    function changeModelImage(index) {
        $('#model .model-images .model-image').hide();
        $('#' + index).show();
    }

    // ------- Model Click Drag
    $('#model .model-images').bind('mouseup touchend', function () {
        modelClick = false;
        $('.model-images').css('cursor', 'grab');

    });

    $('#model .model-images').bind('mousedown touchstart', function (event) {
        $('.model-images').css('cursor', 'grabbing');
        modelClick = true;
        clickedXaxis = event.pageX - $(this).offset().left;
        if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
            clickedXaxis = event.originalEvent.touches[0].pageX - $(this).offset().left;
        }

        $(this).bind('touchmove mousemove', function (event) {
            if (modelClick) {
                currentXaxis = event.pageX - $(this).offset().left;
                if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                    currentXaxis = event.originalEvent.touches[0].pageX - $(this).offset().left;
                }
                if (currentXaxis >= (clickedXaxis + (modelGeneralWidth / numModel) / 2)) {
                    clickedXaxis = currentXaxis;
                    currentIndex++;
                    if (currentIndex >= numModel) {
                        currentIndex = 0;
                    }
                    changeModelImage(imageIDs[currentIndex]);
                }

                if (currentXaxis <= (clickedXaxis - (modelGeneralWidth / numModel) / 2)) {
                    clickedXaxis = currentXaxis;
                    currentIndex--;
                    if (currentIndex < 0) {
                        currentIndex = numModel - 1;
                    }
                    changeModelImage(imageIDs[currentIndex]);
                }
            }
        });
    });
}


// PROJECT DETAIL TAB
// ------------------------------------------------------------
if ($('.project-detail-field').length) {
    let projectDetail = $('.project-detail-field')
    let projectTabSelector = $('.project-detail-field .tab-field .container .wrapper .tab-content ul li')
    let projectTab = $('.project-detail-field .main-content .main-container .tab')
    let projectTabID, projectMainTabID
    projectTabSelector.click(function () {
        projectTab.removeClass('active')
        projectTabSelector.removeClass('active')
        $(this).addClass('active')
        projectTabID = $(this).attr('data-id')
        $.each(projectTab, function (key, value) {
            projectMainTabID = $(this).attr('data-id')
            if (projectTabID == projectMainTabID) {
                $(this).addClass('active')
                if (windowWidth < 769) {
                    projectDetail.css({ 'height': $(this).height() })
                }
            }
        })
    })

    // PROJECT MAIN CONTENT HEIGHT
    let projectMainContent = $('.project-detail-field .main-content')
    let projectTabField = $('.project-detail-field .tab-field')
    if (windowWidth > 768) {
        projectMainContent.css({
            'height': 'calc(100vh - ' + projectTabField.height() + 'px',
        })
    }
    // PROJECT 3D - VESSEL
    let projectThreeD = $('.project-detail-field .main-content .main-container .tab--four .wrapper .text-container .text-wrapper .right-field .button-field ul li')
    let projectThreeVessel = $('.project-detail-field .main-content .main-container .tab--four .wrapper .image-content-field .image-content')
    let projectThreeDID, projectThreeContentID
    projectThreeD.click(function () {
        projectThreeVessel.removeClass('active')
        projectThreeD.removeClass('active')
        $(this).addClass('active')
        projectThreeDID = $(this).attr('data-three')
        $.each(projectThreeVessel, function (key, value) {
            projectThreeContentID = $(this).attr('data-three')
            if (projectThreeDID == projectThreeContentID) {
                $(this).addClass('active')
            }
        })
    })

    // PROJECT TECHNICAL
    let projectDetailTable = $('.project-detail-field .main-content .main-container .tab--two .wrapper .text-container .text-wrapper .right-field .button-field')
    let projectTechicTab = $('.project-detail-field .main-content .main-container .tab--two')
    if (windowWidth > 768) {
        projectDetailTable.css({
            'max-height': projectTechicTab.height() / 2
        })
    }

    // PROJECT - TWO
    let projectTwo = $('.project-detail-field .main-content .main-container .tab--two')
    let projectTwoText = $('.project-detail-field .main-content .main-container .tab--two .wrapper .text-container')
    if (windowWidth < 769) {
        console.log(projectTwoText.height())
        projectTwo.css({
            'height': projectTwoText.height() + $('header').height() + $('header').height(),
        })
        projectTwoText.css({
            'margin-top': $('header').height() / 2
        })
    }
}

